@font-face {
  font-family: Futura;
  src: url(../media/fonts/FuturaBT-Book.ttf); }

#prices table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  table-layout: fixed;
  font-family: Futura; }

#prices table caption {
  font-size: 1.5em;
  margin: .5em 0 .75em; }

#prices table tr {
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  padding: .35em; }

#prices table th,
#prices table td {
  padding: .625em;
  text-align: bold; }

#prices table th {
  font-size: 1em;
  text-transform: uppercase;
  font-size: 1vw;
  font-weight: 800; }

@media screen and (max-width: 600px) {
  #prices table {
    border: 0; }
  #prices table caption {
    font-size: 1.3em; }
  #prices table thead {
    border: none;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px; }
  #prices table tr {
    border-bottom: 3px solid #ddd;
    display: block;
    margin-bottom: .625em; }
  #prices table td {
    border-bottom: 1px solid #ddd;
    display: block;
    font-size: .8em;
    text-align: right; }
  #prices table td::before {
    /*
      * aria-label has no advantage, it won't be read inside a table
      content: attr(aria-label);
      */
    content: attr(data-label);
    float: left;
    font-weight: bold;
    text-transform: uppercase; }
  #prices table td:last-child {
    border-bottom: 0; } }

.form-wrapper-outer {
  padding: 40px;
  border-radius: 8px;
  margin: auto;
  width: 460px;
  margin-top: 7%; }

.form-button {
  text-align: right; }

.field-wrapper {
  position: relative;
  margin-bottom: 23px; }

.field-wrapper input {
  border: 1px solid #62259d;
  padding: 15px;
  border-radius: 4px;
  width: 100%; }

.field-wrapper .field-placeholder {
  font-size: 16px;
  position: absolute;
  bottom: 17px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  color: #62259d;
  left: 8px;
  padding: 0 8px;
  -webkit-transition: transform 150ms cubic-bezier(0.4, 0, 0.2, 1), opacity 150ms cubic-bezier(0.4, 0, 0.2, 1);
  -webkit-transition: opacity 150ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-transform 150ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: opacity 150ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-transform 150ms cubic-bezier(0.4, 0, 0.2, 1);
  -o-transition: transform 150ms cubic-bezier(0.4, 0, 0.2, 1), opacity 150ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: transform 150ms cubic-bezier(0.4, 0, 0.2, 1), opacity 150ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: transform 150ms cubic-bezier(0.4, 0, 0.2, 1), opacity 150ms cubic-bezier(0.4, 0, 0.2, 1), -webkit-transform 150ms cubic-bezier(0.4, 0, 0.2, 1);
  z-index: 1;
  text-align: left;
  width: 100%; }

.field-wrapper input:focus {
  color: #00953a;
  border: 2px solid #00953a;
  outline: none; }

.field-wrapper .field-placeholder span {
  background: #ffffff;
  padding: 0px 8px; }

.field-wrapper input:not([disabled]):focus ~ .field-placeholder {
  color: #00953a; }

.field-wrapper input:not([disabled]):focus ~ .field-placeholder,
.field-wrapper.hasValue input:not([disabled]) ~ .field-placeholder {
  -webkit-transform: scale(0.75) translateY(-39px) translateX(-85px);
  -ms-transform: scale(0.75) translateY(-39px) translateX(-85px);
      transform: scale(0.75) translateY(-39px) translateX(-85px); }

.btn-send {
  background-color: #62259d;
  height: 50px;
  border: 1px solid #62259d; }

.btn-send:hover {
  background-color: #00953a;
  height: 50px;
  border: 1px solid #00953a; }

/*Modal*/
.modal-open .modal {
  overflow-x: hidden;
  overflow-y: auto; }

.modal {
  z-index: 1072; }

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: none;
  overflow: hidden;
  outline: 0; }

.fade {
  -webkit-transition: opacity .15s linear;
  -o-transition: opacity .15s linear;
  transition: opacity .15s linear; }

.modal-dialog-right-side {
  position: absolute;
  top: 0;
  right: 0;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  min-height: 100%;
  margin: 0 !important; }

.modal-content-right-side {
  border: none !important;
  border-radius: 0px; }

.modal-header-right-side {
  border-bottom: none !important; }

.modal-body-right-side {
  padding: 0.3rem 1rem 0rem 1rem  !important; }

.modal-footer-right-side {
  border-top: none !important;
  padding: 0rem 1rem 1rem 1rem !important; }

.modal-backdrop {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1040;
  background: #62259d;
  /* Old browsers */
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, #62259d 0%, #00953a 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#62259d), to(#00953a));
  background: -o-linear-gradient(top, #62259d 0%, #00953a 100%);
  background: linear-gradient(to bottom, #62259d 0%, #00953a 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#62259d', endColorstr='#00953a',GradientType=0 );
  /* IE6-9 */ }

@media (max-width: 400px) {
  .field-wrapper input:not([disabled]):focus ~ .field-placeholder,
  .field-wrapper.hasValue input:not([disabled]) ~ .field-placeholder {
    -webkit-transform: scale(0.75) translateY(-39px) translateX(-70px);
    -ms-transform: scale(0.75) translateY(-39px) translateX(-70px);
        transform: scale(0.75) translateY(-39px) translateX(-70px); } }



